import VueRouter from 'vue-router';
import Vue from 'vue';

import DashboardLayout from '../layout/Admin/DashboardLayout.vue'
// GeneralViews
import NotFound from '../pages/NotFoundPage.vue'

import store from '../store';
// Admin pages
import Dashboard from 'src/pages/admin/Dashboard.vue'
import UserProfile from 'src/pages/admin/UserProfile.vue'
import Playlists from 'src/pages/admin/Playlists.vue'
import Playlist from 'src/pages/admin/Playlist.vue'
import Maps from 'src/pages/admin/Maps.vue'

import VisitorContainer from '../layout/Visitors/VisitorContainer.vue';

//Auth
import Login from '../pages/auth/Login.vue';

import route from './routes';

const routes = [
  {
    path: '/',
    component: Login,
    redirect: route.auth.login.route
  },
  {
    path: route.admin.container.route,
    name: route.admin.container.name,
    component: DashboardLayout,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: route.admin.dashboard.route,
        name: route.admin.dashboard.name,
        component: Dashboard
      },
      {
        path: route.admin.userProfile.route,
        name: route.admin.userProfile.name,
        component: UserProfile
      },
      {
        path: route.admin.playlists.route,
        name: route.admin.playlists.name,
        component: Playlists
      },
      {
        path: `${route.admin.playlist.route}/:id`,
        name: route.admin.playlist.name,
        component: Playlist
      },
      {
        path: route.admin.map.route,
        name: route.admin.map.name,
        component: Maps
      }
    ]
  },
  {
    path: route.auth.login.route,
    component: Login,
    name: route.auth.login.name

  },
  { path: '*', component: NotFound }
]

// configure router
const router = new VueRouter({
  routes, // short for routes: routes
  mode: 'history',
  linkActiveClass: 'nav-item active',
  scrollBehavior: (to) => {
    if (to.hash) {
      return {selector: to.hash}
    } else {
      return { x: 0, y: 0 }
    }
  }
});

Vue.use(VueRouter);

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(x => x.meta.requiresAuth);

  if (requiresAuth && !store.state.auth.userId) {
    next('/login')
  } else if (requiresAuth && store.state.auth.userId && localStorage.getItem('user-token')) {
    next()
  } else {
    next()
  }
});

export default router
