<template>
  <card v-if="accountInfo">
    <h4 slot="header" class="card-title">Profile Info</h4>
    <form>
      <div class="row">
        <div class="col-md-5">
          <base-input
            type="text"
            label="Hospital"
            :disabled="true"
            placeholder="Hospital Name"
            v-model="accountInfo.hospitalInfo.name"
          >
          </base-input>
        </div>
        <div class="col-md-3">
          <base-input
            type="text"
            label="Username"
            placeholder="Username"
            v-model.trim="accountInfo.userInfo.username"
          >
          </base-input>
        </div>
        <div class="col-md-4">
          <base-input
            type="email"
            label="Email"
            placeholder="Email"
            v-model.trim="accountInfo.userInfo.email"
          >
          </base-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6">
          <base-input
            type="text"
            label="First Name"
            placeholder="First Name"
            v-model.trim="accountInfo.userInfo.firstName"
          >
          </base-input>
        </div>
        <div class="col-md-6">
          <base-input
            type="text"
            label="Last Name"
            placeholder="Last Name"
            v-model.trim="accountInfo.userInfo.lastName"
          >
          </base-input>
        </div>
      </div>

      <hr />
      <h4 slot="header" class="card-title">Hospital Info</h4>
      <div class="row">
        <div class="col-md-12">
          <base-input
            type="text"
            label="Address"
            placeholder="Home Address"
            v-model.trim="accountInfo.hospitalInfo.address.street"
          >
          </base-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-4">
          <base-input
            type="text"
            label="City"
            placeholder="City"
            v-model.trim="accountInfo.hospitalInfo.address.city"
          >
          </base-input>
        </div>
        <div class="col-md-4">
          <base-input
            type="text"
            label="Country"
            placeholder="Country"
            v-model.trim="accountInfo.hospitalInfo.address.country"
          >
          </base-input>
        </div>
        <div class="col-md-4">
          <base-input
            type="number"
            label="Postal Code"
            placeholder="ZIP Code"
            v-model.trim="accountInfo.hospitalInfo.address.zipcode"
          >
          </base-input>
        </div>
      </div>

      <div class="text-center">
        <button
          type="button"
          @click="updateProfile"
          class="btn btn-danger btn-fill float-right"
        >
          Update Profile
        </button>
      </div>
      <div class="clearfix"></div>
    </form>
  </card>
</template>
<script>
import Card from "src/components/Cards/Card.vue";
import { mapState } from "vuex";

export default {
  components: {
    Card
  },
  computed: {
    ...mapState("hospitalInfo", ["hospitalInfo"]),
    ...mapState("auth", ["userInfo"])
  },
  mounted() {
    this.accountInfo = {
      hospitalInfo: JSON.parse(JSON.stringify(this.hospitalInfo)),
      userInfo: JSON.parse(JSON.stringify(this.userInfo))
    };
  },
  data() {
    return {
      accountInfo: null,
      error: null
    };
  },
  methods: {
    updateProfile() {
      if (this.validateForm()) {
        if (
          JSON.stringify(this.accountInfo.userInfo) !==
          JSON.stringify(this.userInfo)
        ) {
          this.updateUserInfo();
        }

        if (
          JSON.stringify(this.accountInfo.hospitalInfo) !==
          JSON.stringify(this.hospitalInfo)
        ) {
          this.updateHospitalInfo();
        }
      } else {
        this.$notifications.notify({
          message: `<span><b>Required info missing</b></span>`,
          icon: "nc-icon nc-simple-remove",
          horizontalAlign: "right",
          verticalAlign: "top",
          type: "danger"
        });
      }
    },
    validateForm() {
      const {
        username,
        email,
        firstName,
        lastName
      } = this.accountInfo.userInfo;
      const {
        street,
        city,
        country,
        zipcode
      } = this.accountInfo.hospitalInfo.address;

      return (
        !!username &&
        !!email &&
        !!firstName &&
        !!lastName &&
        !!street &&
        !!city &&
        !!country &&
        !!zipcode
      );
    },
    updateUserInfo() {
      this.$store
        .dispatch("auth/updateUserInfo", this.accountInfo.userInfo)
        .then(() => {
          this.$notifications.notify({
            message: `<span><b>Profile Info Saved Successfully</b></span>`,
            icon: "nc-icon nc-check-2",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "success"
          });
        })
        .catch(error => {
          this.$notifications.notify({
            message: `<span><b>${error.graphQLErrors[0].message}</b></span>`,
            icon: "nc-icon nc-simple-remove",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "danger"
          });
        });
    },
    updateHospitalInfo() {
      this.$store
        .dispatch(
          "hospitalInfo/updateHospitalInfo",
          this.accountInfo.hospitalInfo
        )
        .then(() => {
          this.$notifications.notify({
            message: `<span><b>Hospital Info Saved Successfully</b></span>`,
            icon: "nc-icon nc-check-2",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "success"
          });
        })
        .catch(error => {
          this.$notifications.notify({
            message: `<span><b>${error.graphQLErrors[0].message}</b></span>`,
            icon: "nc-icon nc-simple-remove",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "danger"
          });
        });
    }
  }
};
</script>
<style></style>
