import gql from "graphql-tag";

export default {
  updateAdmin: gql`
    mutation(
      $id: ID!
      $firstName: String
      $lastName: String
      $email: String
      $username: String
    ) {
      updateAdmin(
        id: $id
        firstName: $firstName
        lastName: $lastName
        email: $email
        username: $username
      ) {
        firstName
        lastName
        email
        username
      }
    }
  `
};
