import gql from 'graphql-tag';

export default {
  getHospitalById: gql`
    query($hospitalId: ID!) {
      getHospitalById(hospitalId: $hospitalId) {
        id
        name
        address {
          street
          city
          country
          zipcode
        }
      }
    }
  `,
  getMediVuesByHospitalId: gql`
    query($hospitalId: ID!) {
      getMediVuesByHospitalId(hospitalId: $hospitalId) {
        id
        name
        lng
        lat
      }
    }
  `,
};