<template>
  <div
    class="modal fade"
    id="editMedivue"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content" v-if="medivue">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">
            Edit VueAid Details
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <BaseInput v-model="medivue.name" />
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary btn-fill float-right"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button
            type="button"
            class="btn btn-success btn-fill float-right"
            data-bs-dismiss="modal"
            @click="onSave"
          >
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseInput from "../Inputs/BaseInput.vue";
export default {
  components: {
    BaseInput
  },
  props: {
    medivue: Object
  },
  methods: {
    onSave(e) {
      e.preventDefault();
      this.$emit("onMedivueSave", this.medivue);
    }
  }
};
</script>

