<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-8">
          <edit-profile-form> </edit-profile-form>
        </div>
        <div class="col-md-4">
          <card>
            <h4 slot="header" class="card-title">List of VueAids</h4>

            <table class="table table-hover test">
              <thead>
                <tr>
                  <th
                    scope="col"
                    style="text-align: center !important"
                    class="ps-1"
                  >
                    #
                  </th>
                  <th scope="col" style="text-align: center !important">
                    Name
                  </th>
                  <th scope="col" style="text-align: center !important">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  :key="medivue.id"
                  v-for="(medivue, idx) of medivueInfo"
                  class="text-center"
                >
                  <td scope="row" style="border-bottom: 0 !important;">
                    {{ idx + 1 }}
                  </td>
                  <td style="border-bottom: 0 !important;">
                    {{ medivue.name }}
                  </td>
                  <td
                    class="d-flex justify-content-center"
                    style="border-bottom: 0 !important;"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="23"
                      height="23"
                      fill="black"
                      class="bi bi-pencil me-3"
                      viewBox="0 0 16 16"
                      @click="onEditMedivue(medivue)"
                      data-bs-toggle="modal"
                      data-bs-target="#editMedivue"
                    >
                      <path
                        d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5L13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175l-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z"
                      />
                    </svg>
                  </td>
                </tr>
              </tbody>
            </table>
          </card>
        </div>
      </div>
    </div>
    <EditMedive :medivue="editMedivue" @onMedivueSave="onMedivueSave" />
  </div>
</template>
<script>
import EditProfileForm from "./UserProfile/EditProfileForm.vue";
import UserCard from "./UserProfile/UserCard.vue";
import Card from "src/components/Cards/Card";
import EditMedive from "src/components/AccountSettings/EditMedivue.vue";

import { mapState } from "vuex";

export default {
  components: {
    EditProfileForm,
    UserCard,
    Card,
    EditMedive
  },
  computed: {
    ...mapState("hospitalInfo", ["mediVues"])
  },
  created() {
    this.medivueInfo = this.mediVues;
  },
  data() {
    return {
      medivueInfo: [],
      editMedivue: null
    };
  },
  methods: {
    onEditMedivue(medivue) {
      this.editMedivue = JSON.parse(JSON.stringify(medivue));
    },
    onMedivueSave(medivue) {
      this.$store
        .dispatch("hospitalInfo/updateMedivue", medivue)
        .then(() => {
          this.$notifications.notify({
            message: `<span><b>Medivue Info Saved Successfully</b></span>`,
            icon: "nc-icon nc-check-2",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "success"
          });
        })
        .catch(error => {
          this.$notifications.notify({
            message: error.message,
            icon: "nc-icon nc-simple-remove",
            horizontalAlign: "right",
            verticalAlign: "top",
            type: "danger"
          });
        });
    }
  }
};
</script>
<style></style>
