import gql from "graphql-tag";

export default {
  updateHospitalAddress: gql`
    mutation($hospitalId: ID!, $address: HospitalAddress) {
      updateHospitalAddress(hospitalId: $hospitalId, address: $address) {
        address {
          street
          city
          country
          zipcode
        }
        name
      }
    }
  `
};
