import gql from "../graphql";
import apolloClient from "../apollo";

const initState = {
  hospitalId: "",
  hospitalInfo: null,
  mediVues: []
};

const state = JSON.parse(JSON.stringify(initState));

const mutations = {
  CLEAR_INFO(state) {
    state.hospitalId = "";
    state.hospitalInfo = null;
    state.mediVues = [];
  },
  UPDATE_HOSPITAL_ID(state, hospitalId) {
    state.hospitalId = hospitalId;
  },
  UPDATE_HOSPITAL_INFO(state, hospitalInfo) {
    state.hospitalInfo = JSON.parse(JSON.stringify(hospitalInfo));
  },
  UPDATE_MEDIVUE_LIST(state, medivueArray) {
    state.mediVues = medivueArray;
  },
  UPDATE_MEDIVUE(state, medivue) {
    const idx = state.mediVues.findIndex(
      medivueFromState => medivueFromState.id === medivue.id
    );
    state.mediVues.splice(idx, 1, medivue);
  }
};

const actions = {
  getHospitalInfo({ commit }, hospitalId) {
    return new Promise(async (resolve, reject) => {
      const {
        data: {
          getHospitalById: { __typename, id, ...hospitalInfo }
        }
      } = await apolloClient
        .query({
          query: gql.getHospitalById,
          variables: { hospitalId }
        })
        .catch(err => {
          reject(err);
        });

      commit("UPDATE_HOSPITAL_ID", id);
      commit("UPDATE_HOSPITAL_INFO", hospitalInfo);
      resolve();
    });
  },
  async getMediVues({ commit, state }) {
    const {
      data: { getMediVuesByHospitalId }
    } = await apolloClient.query({
      query: gql.getMediVuesByHospitalId,
      variables: { hospitalId: state.hospitalId }
    });

    commit("UPDATE_MEDIVUE_LIST", getMediVuesByHospitalId);
  },
  async updateHospitalInfo({ commit, state }, hospitalInfo) {
    return new Promise((resolve, reject) => {
      const { __typename, ...address } = hospitalInfo.address;

      apolloClient
        .mutate({
          mutation: gql.updateHospitalAddress,
          variables: { hospitalId: state.hospitalId, address }
        })
        .then(data => {
          commit("UPDATE_HOSPITAL_INFO", data.data.updateHospitalAddress);
          resolve();
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  updateMedivue({ commit }, { lat, lng, __typename, ...medivue }) {
    return new Promise(async (resolve, reject) => {
      await apolloClient
        .mutate({
          mutation: gql.updateMediVue,
          variables: { medivue }
        })
        .then(data => {
          commit("UPDATE_MEDIVUE", data.data.updateMediVue);
          resolve();
        })
        .catch(error => {
          reject(error);
        });
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
